nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 2rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 3rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a {
  border-radius: 50%;
  display: flex;
  font-size: 2rem;
}

nav a.active {
  background: var(--color-bg);
  color: var();
  border: 1px solid var(--color-primary-variant);
}

/* ================ Media Querius (meduim) ==================*/
@media screen and (max-width: 1024px) {
  nav {
    gap: 2rem;
  }
  nav a {
    font-size: 2rem;
  }
}
/* ================ Media Querius (small) ==================*/
@media screen and (max-width: 600px) {
  nav {
    gap: 2rem;
  }
  nav a {
    font-size: 2rem;
  }
}
